import { sidebarContext } from "../../../App";
import {
	useContext,
	useImperativeHandle,
	useState,
	useEffect,
	forwardRef,
} from "react";
import { Db } from "./../../../database/Db_new";
import styles from "./Sidebar_ListViews.module.scss";

function HistoryDisplay(props, ref) {
	const [History, setHistory] = useState([]);
	const context = useContext(sidebarContext);
	useEffect(() => {
		// console.log("display history  from db");
		(async () => {
			let data = await Db.history.orderBy("id").toArray();
			setHistory(data);
		})();
	}, []);
	useImperativeHandle(ref, () => {
		return {
			saveHistory: function (v) {
				// save to db
				Db.transaction("rw", Db.history, async function () {
					await Db.history.add(v);
				})
					.then(async () => {
						setHistory((prevProps) => {
							return [...prevProps, v];
						});
						// console.log("add to history");
					})
					.catch(function (e) {
						console.error(e.stack);
					});
			},
		};
	});
	return (
		<div className={styles["wrapper"]}>
			<ul className={styles.List_view_wrapper}>
				{History?.map((item) => {
					return (
						<li
							className={styles.ViewItem}
							key={item._id}
							onClick={() => {
								context.onItemClick(item._value);
							}}
						>
							{
								/* {item.ArrayOfChineseCharacter.map(
								({ Char_id, char }) => {
									return <span key={Char_id}>{char}</span>;
								}
							)} */
								item._value
							}
						</li>
					);
				})}
			</ul>
			<div className={styles["clear-wrapper"]}>
				<button
					className={styles["clear-htr-btn"]}
					onClick={async () => {
						await Db.history.clear();
						setHistory([]);
					}}
				>
					clear
				</button>
			</div>
		</div>
	);
}

export default forwardRef(HistoryDisplay);
